$gray-0: #fafafa;
$gray-1: #f9f9f9;
$gray-2: #eee;
$gray-3: #ddd;
$gray-4: #ccc;
$gray-5: #bbb;
$gray-6: #878787;
$gray-7: #767676;
$gray-8: #515151;
$gray-9: #313131;

$white: #fff;
$red: #ac4142;
$orange: #d28445;
$yellow: #f4bf75;
$green: #90a959;
$cyan: #75b5aa;
$blue: #268bd2;
$brown: #8f5536;

//https://www.client9.com/css-system-font-stack-sans-serif-v3
// $root-font-family: "IBM Plex Sans", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Droid Sans", "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji","Segoe UI Emoji", "Segoe UI Symbol";

$root-font-family: system-ui, -apple-system, JetBrainsMono-Regular, Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

// poole's variables
$root-font-size: 17px;
$root-font-weight: 400;
// golden ratio https://grtcalculator.com
// 18px @ 33 px, 20px @ 33 px ~ 1.667em
$root-line-height: 1.507em;

$body-color: $gray-8;
$body-bg: #fff;

$border-color: #e5e5e5;

$large-breakpoint: 38em;
$large-font-size: 18px;
$font-scale-dot7: .7rem;
$font-scale-dot8: .8rem;
//

// hyde-hyde
$small-device-font-size: $root-font-size;
$large-device-font-size: $large-font-size;

// https://www.client9.com/css-system-font-stack-monospace-v2
$code-font-family: "JetBrainsMono", "SFMono-Regular", "SF-Mono", Menlo, Monaco, Consolas, "Liberation Mono", "Roboto Mono", "Ubuntu Mono", "Courier New", Courier, monospace;
$code-font-size: .8rem;
$code-fence-font-size: .75rem;
$code-color: green;
$code-background-color: #f9f2f4;
$code-line-height: 1.4;

// links
$link-color: $blue;
$link-hover-color: $body-color;

// section
$section__title-font-size: 2.15rem;

// post
$post__subtitle-font-size: 1.5rem;
$gradient-color-1: #ff2c2c;
$gradient-color-2: #7a5e91;

// post meta
$meta-font-size: $font-scale-dot8;
$meta-font-weight: 300;
$meta-color: $gray-6;

// post tags
$tag-background-color: $gray-2;
$tag-color: #606570;
$tag-font-size: .75rem;
$tag-padding: 4px 10px;
$tag-border-radius: 6px;

// list of posts
$item__date-color: #78716C; // Warm taupe instead of gray
$item__date-font-size: 0.975rem; // Reduced from 1.2rem (21.6px -> 17.5px)
$item__title-big-font-size: 1.8rem;
$item__title-line-height: 1.3;

// heading
$heading-font-weight: 400;
$h1-font-size: 1.75rem;
$h1-line-height: 1.2;
$h2-font-size: 1.45rem;
$h3-font-size: 1.25rem;
$h4-font-size: 1.1rem;
$h5-font-size: 1rem;


// sidebar
$sidebar-color: #5b4d9c;
$sidebar-width: 22rem;

// Post title links (after sidebar-color is defined)
$post-title-link-color: #1C1917; // Dark for post titles
$post-title-link-hover: $sidebar-color; // Purple hover

$site__title-font-size: 2.5rem;
$copyright-font-size: $font-scale-dot7;

// content
$content-max-width: 48rem; // Reduced for better readability
$content-max-width-expanded: 36rem; // Further reduced when sidebar expanded
$content-margin-left: $sidebar-width + 2rem;
$content-margin-right: 2rem;

// sidebar toggle
$sidebar-toggle-bg: #300030;
$sidebar-toggle-bg-hover: #4a0040;
$sidebar-toggle-size: 30px;
$sidebar-toggle-z-index: 1000;
$sidebar-collapse-offset: -($sidebar-width + 0.125rem); // Hide sidebar completely when collapsed
$mobile-content-margin: 2rem; // Left margin on mobile

// home link
$home-link-bg: #f5f5f5;
$home-link-bg-hover: #e0e0e0;
$home-link-color: #333;

// navigation
$navigation-color: #c2255c;

// portfolio
$project__title-font-size: $h2-font-size;
$project__subtitle-font-size-big: $h3-font-size;
$project__subtitle-font-size-small: $h4-font-size;
$project__subtitle-font-style: italic;
$project__subtitle-color: #778492;
$ribbon-color: #276582;
$ribbon-background-color: #479fc8;

// Modern additions
$transition-fast: 0.15s ease;
$transition-base: 0.25s ease;
$item-spacing: 3rem; // Increased spacing for better separation
$separator-color: rgba(0, 0, 0, 0.05); // More subtle separator

// UX Improvements
$border-radius-base: 8px;
$card-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
$card-shadow-hover: 0 8px 15px rgba(0, 0, 0, 0.1);
$transition-hover: transform 0.2s ease, box-shadow 0.2s ease;