// ========================================
// POST LIST - Homepage & Archives
// Hybrid: Modern refinements with Alegreya
// ========================================

.section__title {
    font-size: $section__title-font-size;
    font-weight: 600;
    margin-bottom: 2rem;
}

// ========================================
// INDIVIDUAL POST ITEM
// ========================================

.post-list__item {
    margin-bottom: $item-spacing;
    padding: 2rem;
    background-color: #fff; // Ensure card has background
    border-radius: $border-radius-base;
    box-shadow: $card-shadow;
    transition: $transition-hover;
    border: 1px solid rgba(0, 0, 0, 0.03); // Very subtle border

    &:last-child {
        border-bottom: 1px solid rgba(0, 0, 0, 0.03); // Keep consistent
    }

    // Hide old-style HR separators (we use CSS border instead)
    hr {
        display: none;
    }

    // Card hover effect
    &:hover {
        transform: translateY(-4px);
        box-shadow: $card-shadow-hover;

        .item__title--big a {
            color: $post-title-link-hover;
        }
    }
}

// ========================================
// POST TITLE
// ========================================

.item__title--big {
    display: block;
    font-size: $item__title-big-font-size;
    line-height: $item__title-line-height;
    font-weight: 600;
    margin-bottom: 0.75rem;

    a {
        color: $post-title-link-color;
        text-decoration: none;
        transition: color $transition-fast;

        &:hover {
            color: $post-title-link-hover;
        }
    }
}

.item__title--small {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

// ========================================
// POST DATE
// ========================================

.item__date {
    color: $item__date-color;
    display: block;
    font-size: $item__date-font-size;
    font-weight: 500;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
}

// ========================================
// MODERN PAGINATION
// ========================================

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    margin-top: 3rem;
    padding: 2rem 0;
    border-top: 1px solid $separator-color;

    li {
        list-style: none;
        margin: 0;
    }

    a,
    span {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        min-width: 2.5rem;
        height: 2.5rem;
        padding: 0 0.75rem;
        background: #F5F5F4;
        border: 1px solid $separator-color;
        border-radius: 6px;
        font-weight: 600;
        font-size: 0.9rem;
        color: #57534E;
        transition: all $transition-fast;
        text-decoration: none;

        &:hover {
            background: $sidebar-color;
            color: white;
            border-color: $sidebar-color;
            transform: translateY(-2px);
            box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
        }
    }

    // Current page indicator
    .active {
        background: $sidebar-color;
        color: white;
        border-color: $sidebar-color;
        font-weight: 700;
    }

    // Ellipsis styling
    span:not(.active) {
        background: transparent;
        border-color: transparent;
        cursor: default;
        min-width: auto;
        padding: 0 0.25rem;

        &:hover {
            background: transparent;
            border-color: transparent;
            transform: none;
            box-shadow: none;
            color: #57534E;
        }
    }
}

// ========================================
// RESPONSIVE
// ========================================

@media (max-width: 767px) {
    .post-list__item {
        margin-bottom: 1.5rem;

        &:hover {
            transform: none;
        }
    }

    .item__title--big {
        font-size: 1.5rem;
    }

    .pagination {
        flex-wrap: wrap;
        gap: 0.25rem;

        a,
        span {
            min-width: 2rem;
            height: 2rem;
            padding: 0 0.5rem;
            font-size: 0.85rem;
        }
    }
}