.sidebar {
    background-color: $sidebar-color;
    color: rgb(255, 255, 255);
    color: rgba(255, 255, 255, 0.5);
    padding: 18rem 1rem;
    text-align: center;

    a {
        color: $gray-1;
        border: none;
        transition: color 0.2s ease;

        &:hover {
            color: $link-color;
        }

        &:focus {
            color: $link-color;
        }
    }

    .sidebar-about {
        text-align: center;
    }

    .author-image {
        display: block;
        margin: 0 auto 1.5rem auto; // Center and add bottom margin
        border-radius: 50%; // Make it circular
        transition: transform 0.3s ease;

        &:hover {
            transform: scale(1.05);
        }
    }
}

.sidebar-nav {
    text-align: center;
    list-style: none;
    margin-bottom: 2rem;
    margin-top: 2rem;
    padding-left: 0;
}

.sidebar-nav-item {
    display: block;
    line-height: 1.75;
    margin-bottom: 0.5rem; // Add spacing between items

    a {
        display: block;
        padding: 0.5rem 1rem;
        border-radius: 4px;
        transition: background-color 0.2s ease, color 0.2s ease;

        &:hover {
            background-color: rgba(255, 255, 255, 0.1); // Subtle hover background
            text-decoration: none;
        }
    }

    .active {
        font-weight: bold;
        background-color: rgba(255, 255, 255, 0.15);
    }
}

.site__title {
    font-size: $site__title-font-size;
    margin-bottom: 0.5rem;

    a:hover {
        border: none;
    }
}

.site__description {
    font-size: 0.885rem;
    font-weight: 300;
    font-style: italic;
}

.social {
    text-align: center;

    a {
        padding: 0 4px;
        @include link-no-decoration();
    }
}

.img--circle {
    border-radius: 100%;
}

.img--headshot {
    height: 160px; // Increased size
    width: 160px; // Increased size
}

.img--caption {
    font-style: italic;
}

%small-center-text {
    font-size: $font-scale-dot7;
    line-height: 1.1rem;
    text-align: center;
}

.copyright {
    padding-top: 1rem;
    @extend %small-center-text;
}

.builtwith {
    padding-top: .2rem;
    @extend %small-center-text;
}

// Sidebar toggle functionality
// Override default content width
.content .container {
    max-width: $content-max-width;
}

// Default collapsed state - sidebar hidden, content centered
.sidebar {
    transform: translateX($sidebar-collapse-offset);
}

.content {
    // Center content when sidebar is collapsed
    margin-left: auto;
    margin-right: auto;
    padding-left: 2rem;
    padding-right: 2rem;
    transform-origin: left center;
}

// Sidebar toggle button
.sidebar-toggle {
    position: fixed;
    left: 0;
    top: 40%;
    transform: translateY(-50%);
    width: $sidebar-toggle-size;
    height: $sidebar-toggle-size;
    background: $sidebar-toggle-bg;
    color: white;
    border: none;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    z-index: $sidebar-toggle-z-index;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        background: $sidebar-toggle-bg-hover;
    }
}

// When expanded (sidebar-expanded class added)
.sidebar-expanded {
    .sidebar {
        transform: translateX(0);
    }

    .content {
        // Position content next to sidebar when expanded
        margin-left: $sidebar-width + 2rem; // Sidebar width + spacing
        margin-right: 2rem;
        padding-left: 2rem;
        padding-right: 2rem;

        .container {
            max-width: $content-max-width;
        }
    }

    .sidebar-toggle {
        left: $sidebar-width - 2rem;
    }
}

// Smooth transitions
.sidebar,
.content {
    transition: all 0.3s ease;
}

// Mobile responsive
@media (max-width: 767px) {
    .sidebar-toggle {
        display: none !important;
    }

    .sidebar {
        transform: none !important;
    }

    .content {
        // Center content on mobile with proper margins
        margin-left: auto !important;
        margin-right: auto !important;
        padding-left: 1rem !important;
        padding-right: 1rem !important;
    }

    .sidebar-expanded .content {
        // Same centering for mobile even when expanded class is present
        margin-left: auto !important;
        margin-right: auto !important;
        padding-left: 1rem !important;
        padding-right: 1rem !important;
    }
}

// Home link styling
.home-button {
    margin-bottom: 1rem;
}

.home-link {
    display: inline-block;
    padding: 0.5rem 1rem;
    background: $home-link-bg;
    color: $home-link-color;
    text-decoration: none;
    border-radius: 4px;
    font-size: 0.9rem;
    transition: background-color 0.2s ease;

    &:hover {
        background: $home-link-bg-hover;
        color: $home-link-color;
    }
}